import request from '@/utils/request'

export function fetchPage(query) {
  return request({
    url: '/product/page',
    method: 'get',
    params: query
  })
}

export function add(data) {
  return request({
    url: '/product/add',
    method: 'post',
    data
  })
}

export function edit(data) {
  return request({
    url: '/product/edit',
    method: 'post',
    data
  })
}

export function deleteBatch(data) {
  return request({
    url: '/product/delete',
    method: 'post',
    data
  })
}