<template>
  <div class="app-container">
    <div class="filter-container">
      <el-card>
        <el-form :inline="true">
          <el-form-item label="名字">
            <el-input v-model="pageBO.name" placeholder="输入" style="width: 300px" class="filter-item">
            </el-input>
          </el-form-item>

          <el-form-item label="描述">
            <el-input v-model="pageBO.description" placeholder="输入" style="width: 300px" class="filter-item">
            </el-input>
          </el-form-item>

          <div class="filter-button-group">
            <el-button v-waves class="filter-item" type="primary" icon="el-icon-search" @click="findData">
              搜索
            </el-button>

            <el-button v-waves class="filter-item" icon="el-icon-delete" @click="clearSearch">
              重置
            </el-button>
          </div>
        </el-form>

      </el-card>
    </div>

    <el-card>
      <el-row class="operator-button-row">
        <el-button type="primary" @click="handleCreate()">
          新建
        </el-button>

        <el-button type="danger" @click="deleteDataAccurateBatch()">
          删除
        </el-button>
      </el-row>

      <el-table
          :key="tableKey"
          :data="listResponse"
          border
          fit
          highlight-current-row
          @selection-change="handleSelectionChange"
          :header-cell-style="{background:'#F8F8F8'}"
          style="width: 100%;"
      >

        <el-table-column
            type="selection"
            width="55">
        </el-table-column>

        <el-table-column label="名字" align="center" min-width="150px">
          <template slot-scope="{row}">
            <span>{{ row.name }}</span>
          </template>

        </el-table-column>

        <el-table-column label="描述" min-width="150px">
          <template slot-scope="{row}">
            <span>{{ row.description }}</span>
          </template>
        </el-table-column>

        <el-table-column label="单价" min-width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.unitPrice }}</span>
          </template>
        </el-table-column>

        <el-table-column label="库存数量" min-width="150px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.stockQuantity }}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作" align="center" width="230" class-name="small-padding fixed-width">
          <template slot-scope="{row,$index}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)">
              编辑
            </el-button>

            <el-button size="mini" type="danger" @click="deleteDataAccurate(row, $index)">
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <pagination v-show="totalSize>0" :total="totalSize" :page.sync="pageBO.currentPage" :limit.sync="pageBO.limit"
                @pagination="getPage" :limit="10"/>

    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form ref="dataForm" :rules="rules" :model="dialogData" label-position="left" label-width="70px"
               style="width: 400px; margin-left:50px;">

        <el-form-item label="名字">
          <el-input v-model="dialogData.name" placeholder="输入" style="width: 300px" class="filter-item">
          </el-input>
        </el-form-item>

        <el-form-item label="描述">
          <el-input v-model="dialogData.description" placeholder="输入" style="width: 300px" class="filter-item">
          </el-input>
        </el-form-item>

        <el-form-item label="单价">
          <el-input v-model="dialogData.unitPrice" placeholder="输入" style="width: 300px" class="filter-item">
          </el-input>
        </el-form-item>

        <el-form-item label="库存数量">
          <el-input v-model="dialogData.stockQuantity" placeholder="输入" style="width: 300px" class="filter-item">
          </el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">
          取消
        </el-button>
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">
          确定
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  fetchPage,
  add,
  edit,
  deleteBatch
} from '@/api/product'
import variables from '@/styles/variables.scss'
import waves from '@/directive/waves' // waves directive
import Pagination from '@/components/Pagination' // secondary package based on el-pagination

export default {
  name: 'ProductList',
  components: {Pagination},
  directives: {waves},
  filters: {
    statusFilter(status) {
      const statusMap = {
        published: 'success',
        draft: 'info',
        deleted: 'danger'
      }
      return statusMap[status]
    },
  },
  data() {
    return {
      tableKey: 0,
      listResponse: null,
      listMultipleSelection: [],
      totalSize: 0,
      pageBO: {
        currentPage: 0,
        pageSize: 10,
        name: undefined,
        description: undefined,
        // sort: '+id'
      },
      pageResultList: {},
      dialogData: {
        id: '',
        name: '',
        description: '',
        unitPrice: '',
        stockQuantity: '',
      },
      dialogOldData: null,
      dialogFormVisible: false,
      dialogStatus: '',
      textMap: {
        update: '编辑',
        create: '创建'
      },
      dialogPvVisible: false,
      rules: {
        name: [{required: true, message: '商品名是必填的', trigger: 'change'}],
      },
      variables() {
        return variables
      },
    }
  },
  created() {
    this.findData()
  },

  methods: {
    handleSelectionChange(val) {
      this.listMultipleSelection = val;
    },

    getPage() {
      fetchPage(this.pageBO).then(response => {
        this.listResponse = response.data.dataList
        this.totalSize = response.data.totalSize
      })
    },
    findData() {
      this.getPage()
    },

    clearSearch() {
      this.pageBO.name = undefined
      this.pageBO.description = undefined
    },
    resetTemp() {
      this.dialogData = {
        id: '',
        name: '',
        description: '',
        unitPrice: '',
        stockQuantity: '',
      }
    },

    handleCreate() {
      this.resetTemp()
      this.dialogStatus = 'create'
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs['dataForm'].clearValidate()
      })
    },

    createData() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          add(this.dialogData).then(() => {
            this.dialogFormVisible = false
            this.$notify({
              title: '成功',
              message: '创建成功',
              type: 'success',
              duration: 2000
            })
            this.findData()
          })
        }
      })
    },

    handleUpdate(row) {
      this.dialogData = Object.assign({}, row) // copy obj
      this.dialogOldData = Object.assign({}, row) // copy obj
      this.dialogStatus = 'update'
      this.dialogFormVisible = true
      this.$nextTick(() => {
        this.$refs['dataForm'].clearValidate()
      })
    },

    updateData() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          let requestBody = this.dialogData;
          edit(requestBody).then(() => {
            this.dialogFormVisible = false
            this.$notify({
              title: '成功',
              message: '编辑成功',
              type: 'success',
              duration: 2000
            })
            this.findData()
          })
        }
      })
    },

    deleteDataAccurate(row) {
      const idList = [];
      idList.push(row.id);
      deleteBatch(idList)
          .then(() => {
            this.$notify({
              title: '成功',
              message: '删除成功',
              type: 'success',
              duration: 2000
            })
            this.findData()
          })
    },

    deleteDataAccurateBatch() {
      const idList = this.listMultipleSelection.map(obj => obj.id);
      deleteBatch(idList)
          .then(() => {
            this.$notify({
              title: '成功',
              message: '删除成功',
              type: 'success',
              duration: 2000
            })
            this.findData()
          })
    },
  }
}
</script>
<style lang="scss" scoped>

</style>
